<template>
  <div class="main">
    <van-sticky>
      <van-search
        @search="onSearch"
        v-model="title"
        shape="round"
        show-action
        background="#1989fa"
        placeholder="请输入搜索关键词"
      >
        <template #left>
          <van-icon name="arrow-left" size="26" color="#fff" @click="$router.go(-1);" />
        </template>
        <template #action>
          <span @click="onSearch" style="color:#fff;font-size:16px;">搜索</span>
        </template>
      </van-search>
    </van-sticky>
    <div class="content">
      <van-list ref="list" error-text="请求失败，点击重新加载">
        <van-panel
          class="list-item"
          v-for="(item,index) in list"
          :key="index"
          @click="getArticlesDetail(item._id)"
        >
          <template #header>
            <div class="item-header">
              <span>{{(index+1)+"、"+item.title}}</span>
              <van-icon size="24" name="like-o" v-if="userInfo" />
            </div>
          </template>
          <div class="item-content">
            <lazy-component
              class="image"
              v-for="(imageUrl,imageIndex) in item.thumbnails"
              :key="imageIndex"
            >
              <van-image class="image" lazy-load :src="imageUrl">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
            </lazy-component>
          </div>
          <template #footer>
            <div class="item-footer">
              <span>{{item.category}}</span>
              <span>{{$formatTime(item._createTime)}}</span>
            </div>
          </template>
        </van-panel>
        <van-cell class="bottom" v-show="list.length>2" />
      </van-list>
    </div>
    <van-row class="footer" v-show="totalCount>0">
      <van-col span="21">
        <van-pagination
          @change="onPageChange"
          v-model="currentPage"
          :total-items="totalCount"
          :show-page-size="5"
          :items-per-page="pageSize"
          force-ellipses
        />
      </van-col>
      <van-col span="3">
        <van-button type="info" @click="showCustomPageDialog">指定页</van-button>
      </van-col>
    </van-row>

    <van-popup v-model="isShowCustomPageDialog">
      <van-row>
        <van-col span="6" align="center">
          <van-button size="small" type="info" @click="selectCustomPage(1)">《首页</van-button>
        </van-col>
        <van-col span="6" align="center">
          <van-field v-model="showCustomPage" size="small" type="digit" />
        </van-col>
        <van-col span="6" align="center">
          <van-button size="small" type="info" @click="selectCustomPage(showCustomPage)">跳转》</van-button>
        </van-col>
        <van-col span="6" align="center">
          <van-button size="small" type="info" @click="selectCustomPage(totalPage)">尾页》</van-button>
        </van-col>
      </van-row>
    </van-popup>
    <van-overlay :show="showLoading" @click="showLoading = false">
      <div class="wrapper">
        <van-loading color="#fff" size="60" />
      </div>
    </van-overlay>
  </div>
</template>

<script>
import {
  Tabs,
  Tab,
  Row,
  Tag,
  Col,
  Icon,
  Cell,
  Popup,
  Sticky,
  List,
  Pagination,
  Panel,
  Loading,
  Image,
  Button,
  Card,
  Overlay,
  Search,
  Field,
  Toast
} from "vant";

export default {
  components: {
    [Tabs.name]: Tabs,
    [Tab.name]: Tab,
    [Row.name]: Row,
    [Tag.name]: Tag,
    [Col.name]: Col,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Icon.name]: Icon,
    [Popup.name]: Popup,
    [Loading.name]: Loading,
    [Sticky.name]: Sticky,
    [List.name]: List,
    [Panel.name]: Panel,
    [Image.name]: Image,
    [Button.name]: Button,
    [Card.name]: Card,
    [Search.name]: Search,
    [Overlay.name]: Overlay,
    [Pagination.name]: Pagination
  },

  data() {
    return {
      tabs: [],
      list: [],
      isShowCustomPageDialog: false,
      showCustomPage: 0,
      showMenu: false,
      showLoading: false,
      totalPage: 0,
      totalCount: 0,
      totalPage: 0,
      pageSize: 10,
      currentPage: 1,
      tabIndex: 0,
      userInfo: null,
      endTime: "",
      title: ""
    };
  },
  created() {},
  methods: {
    hangleGoUp() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    onSearch() {
      this.currentPage = 1;
      this.getArticles();
    },
    getArticles() {
      let param = {
        pageIndex: this.currentPage - 1, //页码
        pageSize: this.pageSize,
        category: "",
        title: this.title,
        type: 0
      };
      this.showLoading = true;
      http
        .post("/article/paging", param, {
          headers: { token: this.$getToken() }
        })
        .then(res => {
          // console.log(res);
          this.showLoading = false;
          if (res.status == 0) {
            if (this.currentPage - 1 == res.data.pageIndex) {
              this.totalPage = this.pageCount(
                res.data.totalCount,
                res.data.pageSize
              );
              this.totalCount = res.data.totalCount;
              this.list = res.data.list;
              this.hangleGoUp();
            }
          }
        });
    },
    pageCount(totalnum, limit) {
      return totalnum > 0
        ? totalnum < limit
          ? 1
          : totalnum % limit
          ? parseInt(totalnum / limit) + 1
          : totalnum / limit
        : 0;
    },
    getArticlesDetail(detailId) {
      let param = {
        _id: detailId
      };
      this.showLoading = true;
      http
        .post("/article/detail", param, {
          headers: { token: this.$getToken() }
        })
        .then(res => {
          this.showLoading = false;
          if (res.status == 0) {
            if (res.data && res.data.title) {
              localStorage.setItem("article_detail", JSON.stringify(res.data));
              window._article_detail = res.data;
              this.$router.push("detail");
            }
          }
        });
    },
    onTabChange(index) {
      this.currentPage = 1;
      this.tabIndex = index;
      this.getArticles();
    },
    onPageChange() {
      this.getArticles();
    },
    showPopup() {
      if (!this.userInfo) {
        this.showMenu = false;
        return;
      }
      this.showMenu = !this.showMenu;
    },
    selectCustomPage(page) {
      // console.log(page);
      if (page > 0 && page <= this.totalPage) {
        this.currentPage = page;
      } else {
        this.currentPage = this.totalPage;
      }
      this.isShowCustomPageDialog = false;
      this.getArticles();
    },
    showCustomPageDialog() {
      this.showCustomPage = this.currentPage;
      this.isShowCustomPageDialog = !this.isShowCustomPageDialog;
    }
  }
};
</script>

